import React, { useRef } from 'react';

import PopupWithForm from './PopupWithForm';

function EditAvatarPopup({ isOpen, onClose, onUpdateAvatar }) {
  // –ò—Å–ø–æ–ª—å–∑—É—é —Ä–µ—Ñ—ã
  const inputRef = useRef();

  const handleSubmit = e => {
    e.preventDefault();

    onUpdateAvatar({
      avatar: inputRef.current.value
    });
  };

  return (
    <PopupWithForm title={'–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä'} name={'popup_edit_img'} isOpen={isOpen} onClose={onClose} children={(
      <>
        <label className="popup__form-label">
          <input type="url" className="popup__form-input" id="url-img-edit" placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É" name="imgEdit" required ref={inputRef} />
          <span className="popup__error-message url-img-edit-error"></span>
        </label>
        <button className="button-popup button-popup_edit_img" type="submit" onClick={handleSubmit}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
      </>
    )} />
  );
}

export default EditAvatarPopup;
