import React, { useContext } from 'react';

// –ö–æ–Ω—Ç–µ–∫—Å—Ç
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function Card({ card, onCardClick, onCardLike, onCardDelete }) {
  // –î–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—è
  const { owner, link, name, likes } = card;

  // –ö–æ–Ω—Ç–µ–∫—Å—Ç user
  const { _id } = useContext(CurrentUserContext);

  const toggleClassIcon = () => {
    // –Ø–≤–ª—è–µ—Ç—Å—è –ª–∏ –∫–∞—Ä—Ç–æ—á–∫–∞ –º–æ–µ–π
    return owner._id === _id ? 'card-place__delete-btn' : 'card-place__delete-btn card-place__delete-btn_display_none';
  };

  // –ü—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –µ—Å—Ç—å –ª–∏ –Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–µ –ª–∞–π–∫
  const isLiked = () => {
    // –ù–µ–º–Ω–æ–≥–æ –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏–∏. –ï—Å–ª–∏ –∫ –ø—Ä–∏–º–µ—Ä—É –≤—Å–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–µ –±—É–¥—É—Ç –∏–º–µ—Ç—å –ª–∞–π–∫–æ–≤, —Ç–æ –∏ –ø—Ä–æ–≤–µ—Ä—è—Ç—å –∏—Ö –Ω–µ–∑–∞—á–µ–º
    if (likes.length > 0) {
      // –õ–∞–π–∫–Ω—É–ª –∫–∞—Ä—Ç–æ—á–∫—É –∏–ª–∏ –Ω–µ—Ç
      const isLiked = likes.some(like => like._id === _id);
      return isLiked ? 'card-place__like-btn card-place__like-btn_active' : 'card-place__like-btn';
    }
    return 'card-place__like-btn';
  };

  return (
    <>
      <li className="place__item">
        <div className="card-place">
          <button className={ toggleClassIcon() } onClick={() => onCardDelete(card)}></button>
          <img src={link} alt={name} className="card-place__img" onClick={() => onCardClick(name, link)} />
          <h2 className="card-place__title">
          {name}
          </h2>
          <div className="card-place__likes">
          <button className={ isLiked() } type="button" onClick={() => onCardLike(card)}></button>
          <span className="card-place__counter">{ likes.length }</span>
          </div>
        </div>
      </li>
    </>
    );
}

export default Card;
