import React, { useEffect, useState, useContext } from 'react';
import PopupWithForm from './PopupWithForm';

// –ö–æ–Ω—Ç–µ–∫—Å—Ç
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import { ValidationFormContext } from '../contexts/validationFormContext';

function EditProfilePopup({ isOpen, onClose, onUpdateUser }) {
  // –¢–∞–∫ –∫–∞–∫ –æ–±—å–µ–∫—Ç state –æ–±–Ω–æ–≤–ª—è–µ—Ç—Å—è –Ω–µ –≤–µ—Å—å, –Ω—É–∂–Ω–æ –ø–æ–ª—É—á–∞—Ç—å –ø—Ä–µ–¥–µ–¥—É—â–∏–π state. –ü—Ä–æ—â–µ —Ä–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –¥–≤–∞ –æ—Ç–¥–µ–ª—å–Ω—ã—Ö —Å–æ—Å—Ç–æ—è–Ω–∏—è, —á—Ç–æ–±—ã –æ–Ω–∏ –Ω–µ –∑–∞–≤–∏—Å–µ–ª–∏ –¥—Ä—É–≥ –æ—Ç –¥—Ä—É–≥–∞, –∏ –Ω–µ –ø—Ä–∏—à–ª–æ—Å—å –ø–æ–ª—É—á–∞—Ç—å –ø—Ä–µ–¥–µ–¥—É—â–∏–π state. –í –û–û–ü –ø–æ–¥—Ö–æ–¥–µ –º–æ–∂–µ—Ç –±—ã—Ç—å —Ç–æ–ª—å–∫–æ –æ–¥–∏–Ω –æ–±—ä–µ–∫—Ç state.
  // const [name, setName] = useState('–ñ–∞–∫-–ò–≤ –ö—É—Å—Ç–æ');
  // const [description, setDescription] = useState('–ò—Å—Å–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å –æ–∫–µ–∞–Ω–∞');

  const [profileValue, setProfileValue] = useState({
    profileName: '',
    profileDoes: ''
  });
  const {profileName, profileDoes} = profileValue;

  const [isValidProfileName, setValidProfileName] = useState('false');
  const [isValidProfileDoes, setValidProfileDoes] = useState('false');

  // console.log(profileName, profileDoes)

  // –ö–æ–Ω—Ç–µ–∫—Å—Ç
  const { about, name } = useContext(CurrentUserContext);
  const validation = useContext(ValidationFormContext);

  // –í—Ç–æ—Ä—ã–º –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º –ø–µ—Ä–µ–¥–∞–ª –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏, —Ç–µ —á—Ç–æ –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è –≤ useEffect
  useEffect(() => {
    setProfileValue({
      profileName: about,
      profileDoes: name
    });
  }, [about, name]);

  useEffect(() => {
    const isValidProfileName = validation(profileValue.profileName, 2, 40);
    const isValidProfileDoes = validation(profileValue.profileDoes, 2, 200);
    // console.log(isValidProfileName, isValidProfileDoes)

    setValidProfileName(isValidProfileName);
    setValidProfileDoes(isValidProfileDoes);
  }, [profileValue, validation]);

  // –ù–µ–º–Ω–æ–≥–æ —É—Å–ª–æ–∂–Ω–∏–ª –∑–∞–¥–∞—á—É, —á—Ç–æ–±—ã –ø–æ—Ç—Ä–µ–Ω–∏—Ä–æ–≤–∞—Ç—å—Å—è, –ø–æ–Ω—è—Ç—å –∫–∞–∫ —Ä–µ–∞–ª–∏–∑—É–µ—Ç—Å—è –∑–∞–¥–∞—á–∞ –ø–æ-–¥—Ä—É–≥–æ–º—É
  const handleChange = e => {
    setProfileValue(state => {
      const previousValue = Object.assign({}, state);
      previousValue[e.target.name] = e.target.value;
      return previousValue;
    });
  }

  const handleSbmit = e => {
    e.preventDefault();

    // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
    onUpdateUser(profileValue);
  };

  return (
    <PopupWithForm title={'–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å'} name={'popup_edit_profile'} isOpen={isOpen} onClose={onClose} onSubmit={handleSbmit} children={(
      <>
        <label className="popup__form-label">
          {/* –° –ø–æ–º–æ—â—å—é value –∏ onChange —Å–æ–∑–¥–∞–ª —É–ø—Ä–∞–≤–ª—è—é—â–∏–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç, –≥–¥–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –±–µ—Ä–µ—Ç—Å—è –∏–∑ state –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ */}
          <input type="text" className="popup__form-input popup__form-input_value_name" id="profile-name-input" placeholder="–ò–º—è" name="profileName" minLength="2" maxLength="40" required value={profileName} onChange={handleChange} style={{borderBottom: !isValidProfileName ? '1px solid red' : ''}} />
          <span className={!isValidProfileName ? "popup__error-message profile-name-input-error popup__error-message_active" : "popup__error-message profile-name-input-error"}>–û—à–∏–±–∫–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏</span>
        </label>
        <label className="popup__form-label">
          <input type="text" className="popup__form-input popup__form-input_value_does" id="profile-does-input" placeholder="–î–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç—å" name="profileDoes" minLength="2" maxLength="200" required value={profileDoes} onChange={handleChange} style={{borderBottom: !isValidProfileDoes ? '1px solid red' : ''}} />
          <span className={!isValidProfileDoes ? "popup__error-message profile-does-input-error popup__error-message_active" : "popup__error-message profile-does-input-error"}>–û—à–∏–±–∫–∞ –≤–∞–ª–∏–¥–∞—Ü–∏–∏</span>
        </label>
        <button className="button-popup button-popup_edit_profile" type="submit" disabled={isValidProfileName && isValidProfileDoes ? false : true} style={{opacity: !(isValidProfileName && isValidProfileDoes) ? '.2' : ''}}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
      </>
    )} />
  );
}

export default EditProfilePopup;
